@import url(https://fonts.cdnfonts.com/css/diablo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #161616;
}

#root {
  /* overflow: hidden;*/
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  border-radius: 2px;
  height: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #222222;
  border-radius: 2px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #8f6c32;

  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #c09144;
}

.container {
  /* Height of navbar */
  height: calc(100% - 48px);
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.diablo-text {
  font-family: "Diablo", sans-serif !important;
  font-weight: 900;
  font-style: normal;
}

.caps {
  text-transform: uppercase;
}

.shadow {
  text-shadow: 0px 2px #000000ad;
}

.list-shadow {
  text-shadow: 0px 1px #000000ad;
}

.count-text {
  font-size: 20pt;
}

.runList-container {
  display: flex;
  max-height: 394px;
  margin-top: 15px;
  position: absolute;
  justify-content: center;
  bottom: 15px;
  overflow: auto;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.3);
}

@media only screen and (max-height: 800px) {
  .runList-container {
    max-height: 300px;
  }
}

.listImage-container {
  width: 25px;
  height: 25px;
  overflow: "hidden";
  margin-right: 20px;
}

.autoComplete-container {
  border-style: solid;
  border-color: inherit;
  padding: 15px;
  border-image: url(/d2-run-tracker/static/media/diablo-frame.855a6b1b.png) 140 repeat;

  border-image-width: 150px;
  background-color: rgba(0, 0, 0, 0.24);
  z-index: 0;
  box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.35), inset 0px 0px 6px 20px rgba(0, 0, 0, 0.35);
}

.itemContainer {
  position: relative;
  border-style: solid;
  border-color: inherit;
  padding: 10px;
  border-image: url(/d2-run-tracker/static/media/diablo-frame.855a6b1b.png) 140 repeat;
  border-image-width: 60px;
  background-color: rgba(0, 0, 0, 0.24);
  box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.2);
}

.itemContainer.notFound {
  -webkit-filter: grayscale(1) brightness(1.1);
          filter: grayscale(1) brightness(1.1);
}

.glowing-text {
  -webkit-animation: glow 1s infinite;
          animation: glow 1s infinite;
}

.itemSlider-frame {
  box-shadow: inset 0px 0px 2px 0px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(143, 108, 50, 0.3);
}

@-webkit-keyframes glow {
  0% {
    text-shadow: 0px 0px 0px #4169e1;
  }
  50% {
    text-shadow: 0px 0px 3px #4169e1;
  }
  100% {
    text-shadow: 0px 0px 0px #4169e1;
  }
}

@keyframes glow {
  0% {
    text-shadow: 0px 0px 0px #4169e1;
  }
  50% {
    text-shadow: 0px 0px 3px #4169e1;
  }
  100% {
    text-shadow: 0px 0px 0px #4169e1;
  }
}

.unique {
  color: #a59263;
  font-weight: bold;
}

.set {
  color: green;
  font-weight: bold;
}

.rune {
  color: #ffa500;
  font-weight: bold;
}

.crafting {
  color: #ffa500;
  font-weight: bold;
}

.magic {
  color: #4169e1;
}

.uncategorized {
  color: white;
  font-weight: bold;
}

.totalRunTime {
  position: absolute;
  top: 15px;
  right: 65px;
  z-index: 1101;
}

.runView-container {
  position: relative;
  border-style: solid;
  border-color: inherit;
  padding: 15px;
  border-image: url(/d2-run-tracker/static/media/diablo-frame.855a6b1b.png) 140 repeat;

  border-image-width: 150px;
  background-color: rgba(0, 0, 0, 0.24);
  z-index: 0;
  box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.35), inset 0px 2px 6px 5px rgba(0, 0, 0, 0.35);
}

.lootItem-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 75px;
}

.lootItem-text {
  /* Rip firefox */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  font-size: 10px;
  margin-top: 5px;
  font-weight: bold;
  height: 2.5em;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
}

.item-notFound {
  -webkit-filter: grayscale(0.8);
          filter: grayscale(0.8);
  opacity: 0.4;
}

.item-notFound:hover {
  opacity: 0.6;
  -webkit-filter: grayscale(0.6);
          filter: grayscale(0.6);
}

.loot-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: flex-end;
  flex-flow: wrap;
  width: 100%;
}

.loot-image {
  display: block;
  max-width: 60px;
  max-height: 60px;
  width: auto;
  height: auto;
}

.accordation-details-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.one-socket {
  flex: 0 0 20px; /* explanation below */
  margin: 2px;
  display: block;
  min-block-size: 5px;
  max-width: 5px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(84, 84, 84, 0) 0%, rgba(32, 32, 32, 0.3) 60%, rgba(22, 22, 22, 0.4) 100%);
  border: 1px solid rgba(180, 180, 180, 0.377);
}

.one-socket.summary {
  flex: 0 0 6px;
  margin: 1px;
  min-block-size: 7px;
  max-width: 6px;
}

.socket-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 30px;
}

.background-image {
  position: relative;
  height: 40px;
  width: 40px;
  background-image: url(/d2-run-tracker/static/media/no_image.3a4c2d46.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  align-items: center;
  display: flex;
  justify-content: center;
}

.summaryView-container {
  margin-top: 15px;
  width: 100%;
  height: 100%;
  position: relative;
}

.runStats-container {
  display: flex;
  position: relative;
  grid-gap: 30px;
  gap: 30px;
  margin: 30px;
}

.charImg {
  position: relative;
  z-index: 1;
}

.summary-text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  width: 70px;
}

.charShadow {
  border-radius: 50%;
  background: rgb(14, 14, 14);
  -webkit-filter: blur(7px);
          filter: blur(7px);
  position: absolute;
  opacity: 0.6;
  z-index: 0;
}

