  margin-left: -3px;
  width: 75px;
  height: 25px;
  opacity: 0.5;
}

.charShadow.barbarian {
  bottom: -3px;
  margin-left: 0px;
  width: 80px;
  height: 20px;
}

.charShadow.druid {
  bottom: -3px;
  margin-left: 0px;
  width: 60px;
  height: 20px;
}

.charShadow.necromancer {
  bottom: -3px;
  margin-left: 10px;
  width: 60px;
  height: 20px;
}

.charShadow.paladin {
  bottom: 0px;
  margin-left: 10px;
  width: 80px;
  height: 35px;
}

.charShadow.sorceress {
  bottom: -4px;
  margin-left: -2px;
  width: 60px;
  height: 20px;
}

.alignData {
  text-align: end;
}

.runStats-data {
  display: flex;
  flex-direction: column;
  align-items: space-between;
}

.dataTable {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  margin-top: auto;
}

.scrollableItem-container {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  overflow: hidden;
  box-shadow: inset 0px 0px 12px 9px rgb(0 0 0);
}

.itemBoxes-container {
  display: flex;
  width: 100%;
  grid-gap: 15px;
  gap: 15px;
  flex-basis: 100%;
  flex-wrap: wrap;
}

.emblem {
  background-image: url(/d2-run-tracker/static/media/emblem.4d9d01bf.png);
  height: 330px;
  background-position-x: center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 821.1px;
  position: relative;

  z-index: -1;
}
.emblem-parent {
  position: absolute;
  height: 200px;
  overflow: hidden;
  margin-top: -180px;
}

.override-radius > .MuiInputBase-root {
  border-radius: 0;
  box-shadow: 0px 2px 3px 1px rgba(0, 0, 0, 0.2);
}

.paper-design {
  position: relative;
  border-style: solid;
  border-color: inherit;
  padding: 15px;
  border-image: url(/d2-run-tracker/static/media/diablo-frame.855a6b1b.png) 140 repeat;

  border-image-width: 150px;
  background-color: rgba(0, 0, 0, 0.24);
  z-index: 0;
  box-shadow: 0px 0px 10px 6px rgba(0, 0, 0, 0.35), inset 0px 2px 6px 28px rgba(0, 0, 0, 0.35);
}

.header-link {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
}

.bg {
  position: fixed;
  background-image: url(/d2-run-tracker/static/media/bg-tile.6859b78b.png);

  z-index: -1;
  opacity: 0.03;
}

.my-masonry-grid {
  position: relative;
  z-index: 0; /* For animations */
  display: flex;
  margin-left: -15px; /* gutter size offset */
  width: auto;
  padding: 15px;
}
.my-masonry-grid_column {
  padding-left: 15px; /* gutter size */
}

/* Style your items */
.my-masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 15px;
  background-clip: padding-box;
}

@media (max-width: 800px) {
  .my-masonry-grid {
    margin-left: -10px; /* gutter size offset */
  }
  .my-masonry-grid_column {
    padding-left: 10px; /* gutter size offset */
  }
  .my-masonry-grid_column > div {
    margin-bottom: 10px; /* space between items */
  }
}

.sole-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
}

.scrollingSearch {
  position: fixed;
  z-index: 2;
  width: 120px;
  top: 0;
}

.searchBar-menu--item {
  font-size: 12px !important;
}

.searchBar {
  position: relative;
  border-style: solid;
  border-color: inherit;
  padding: 5px;
  border-image: url(/d2-run-tracker/static/media/custom-frame.93fab528.png) 140 repeat;
  border-image-width: 150px;
  background-color: rgba(0, 0, 0, 0.24);
}

